.featured{
    width: 100%;
    display: flex;
    justify-content: space-between;
}

.featuredItem{
    flex:1;
    margin: 0 20px;
    padding: 30px;
    border-radius: 10px;
    cursor: pointer;
    box-shadow: 0px 0px 5px -1px rgba(0,0,0,0.4);
    -webkit-box-shadow: 0px 0px 5px -1px rgba(0,0,0,0.4);
    -moz-box-shadow: 0px 0px 5px -1px rgba(0,0,0,0.4);
}

.featuredTitle {
    font-size: 20px;
}

.featuredMoneyContainer{
    margin: 10px 0pxss;
    display: flex;
    align-items: center;
}

.featuredMoney{
    font-size: 30px;
    font-weight: 600;
}

.featuredMoneyRate{
    display: flex;
    align-items: center;
    margin-left: 20px;
}

.featuredSub{
    font-size: 15px;
    color: gray;
}

.featuredIcon{
    font-size: 14px;
    margin-left: 5px;
    color: green;;

}

.negative{
    color: red;
}