.newUser{
    flex:4;
    padding: 0 100px;
    
}

.newUserForm{
    display: flex;
    flex-wrap: wrap;
    
}

.newUserItem{
    width: 600px;
    display: flex;
    flex-direction: column;
    margin-top: 10px;
    margin-right: 20px;
}

.newUserItem>label {
    margin-bottom: 10px;
    font-size: 14px;
    font-weight: 600;
    color: rgb(143, 143, 143);
}

.newUserItem>input{
    height: 20px;
    padding: 10px;
    border-radius: 5px;
    border: 1px solid gray;
}

.newUserGender>input{
    margin-top: 15px;
}

.newUserGender>label{
    margin: 10px;
    font-size: 18px;
    color: #555;
}

.newUserSelect{
    height: 40px;
    border-radius: 5px;
}

.newUserButton{
    width: 200px;
    border: none;
    border-radius: 10px;
    background-color: darkblue;
    color:white;
    margin-top: 30px;
    padding: 7px 10px;
    font-weight: 600;
}